import React, { Component } from "react";
import Overview from "./component/Overview";
import 'bootstrap/dist/css/bootstrap.css';
import Background from './cv.png';
import Logo from './logo.png';

const textColor = {
  color: '#000080',
  fontWeight: 'bolder'
}
const textColor1 = {
  color: '#FFFF00',
  fontWeight: '900',
  fontSize: '24px'
}
const textColor2 = {
  color: '#CD5C5C',
  fontWeight: '900',
  fontSize: '28px'
}

class App extends Component {
  constructor(){
    super();

    this.state = {
      name: "",
      email: "",
      phone: "",
      school: "",
      study: "",
      studydate: "",
      company: "",
      position: "",
      task: "",
      from: "",
      to: "",

      general: [],
      education: [],
      experience: [],

      isActive: false
    };
  }

  handleChange = (e) => {
    this.setState({
      name: e.target.value,
    });
  };
  handleEmail = (e) => {
    this.setState({
      email: e.target.value,
    });
  };
  handlePhone = (e) => {
    this.setState({
     phone: e.target.value,
    });
  };
  handleSchool = (e) => {
    this.setState({
     school: e.target.value,
    });
  };
  handleStudy = (e) => {
    this.setState({
     study: e.target.value,
    });
  };
 handleStudyDate = (e) => {
    this.setState({
     studydate: e.target.value,
    });
  };
 handleSCompany = (e) => {
    this.setState({
     company: e.target.value,
    });
  };
  handleStudyDate = (e) => {
    this.setState({
     studydate: e.target.value,
    });
  };
  handlePosition = (e) => {
    this.setState({
     position: e.target.value,
    });
  };
  handleTask = (e) => {
    this.setState({
     task: e.target.value,
    });
  };
  handleFrom = (e) => {
    this.setState({
     from: e.target.value,
    });
  };
  handleTo = (e) => {
    this.setState({
     to: e.target.value,
    });
  };

  handleShow = () => {
    this.state({
      isActive: true
    });
  };
  handleHide = () => {
    this.state({
      isActive: false
    });
  };

  onSubmitTask = (e) => {
    e.preventDefault();
    const {name, email, phone, school, study,studydate, company, position, task, from, to} = this.state
    if(name && email && phone && school && study && studydate && company && position && task && from && to){
      this.setState({
        general: this.state.general.concat(this.state.name, this.state.email, this.state.phone),  
        name: "",
        email: "",
        phone: "",
        education: this.state.education.concat(this.state.school, this.state.study, this.state.studydate), 
        school: "",
        study: "",
        studydate: "",
        experience: this.state.experience.concat(this.state.company, this.state.position, this.state.task, this.state.from, this.state.to),
        company: "",
        position: "",
        task: "",
        from: "",
        to: "",
      });
    }
  };
  
  render() {
    const { general, education, experience, isActive} = this.state;

    return (
      <div className="container" style={{backgroundImage: `url("${Background}")`, backgroundRepeat: 'no-repeat', backgroundSize: 'cover'}}>
        <nav className="row navbar navbar-light bg-light d-flex align-items-center">
          <a class="navbar-brand" href="#">
            <img src={Logo} width="100" height="100" class="d-inline-block align-top" alt='logo'/>
            <h1 style={textColor} className="float-right align-middle">UPDATE YOUR CV</h1>
          </a>
        </nav>
        <div className="row">
          <div className="col-sm">
            <form onSubmit = {this.onSubmitTask}>
              <div>
                <h4 style={textColor2}>GENERAL INFOMATION</h4>
                <div className="form-group">
                  <label htmlFor="nameInput" style={textColor1}>Enter Name</label>
                  <input className="form-control" onChange = {this.handleChange} value = {this.state.name} type="text" id="nameInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="emailInput" style={textColor1}>Enter Email</label>
                  <input className="form-control" onChange = {this.handleEmail} value = {this.state.email} type="email" id="emailInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="phoneInput" style={textColor1}>Enter Phone</label>
                  <input className="form-control" onChange = {this.handlePhone} value = {this.state.phone} type="phone" id="phoneInput"></input>
                </div>
              </div>
              <div>
                <h4 style={textColor2}>EDUCATION</h4>
                <div className="form-group">
                  <label htmlFor="schoolInput" style={textColor1}>School</label>
                  <input className="form-control" onChange = {this.handleSchool} value = {this.state.school} type="text" id="schoolInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="studyInput" style={textColor1}>Title of Study</label>
                  <input className="form-control" onChange = {this.handleStudy} value = {this.state.study} type="text" id="studyInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="studyDateInput" style={textColor1}>Date of Study</label>
                  <input className="form-control" onChange = {this.handleStudyDate} value = {this.state.studydate} type="date" id="studyDateInput"></input>
                </div>
              </div>
              <div>
                <h4 style={textColor2}>EXPERIENCE</h4>
                <div className="form-group">
                  <label htmlFor="companyInput" style={textColor1}>Company</label>
                  <input className="form-control" onChange = {this.handleSCompany} value = {this.state.company} type="text" id="companyInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="positionInput" style={textColor1}>Position Title</label>
                  <input className="form-control" onChange = {this.handlePosition} value = {this.state.position} type="text" id="positionInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="taskInput" style={textColor1}>Main Tasks</label>
                  <textarea className="form-control" onChange = {this.handleTask} value = {this.state.task} type="text" id="taskInput"></textarea>
                </div>
                <div className="form-group">
                  <label htmlFor="fromInput" style={textColor1}>From</label>
                  <input className="form-control" onChange = {this.handleFrom} value = {this.state.from} type="date" id="fromInput"></input>
                </div>
                <div className="form-group">
                  <label htmlFor="toInput" style={textColor1}>To</label>
                  <input className="form-control" onChange = {this.handleTo} value = {this.state.to} type="date" id="toInput"></input>
                </div>
              </div>
              <button type="submit" className="form-control btn-success" style={textColor1}>Add Resume</button>
            </form>
          </div>
          <div className="col-sm">
            <Overview general={general} education={education} experience={experience} isActive={isActive} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;